*{
  margin: 0;
  padding: 0;
}

body{
  background: #dfdfdf;
}


.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: white;
  
}

.navbar{
  display: flex;
  align-items: center;
  margin-bottom: 0;
}

.navbar a{
  font-weight: bold;
  font-size: x-large;
  padding-inline: 2vw;
  color: Black;
  font-family: 'Playfair Display', serif;
}

.navbar button{
  border-radius:0.7vw;
  padding: 0.6vw 1vw;
  font-size: x-large;
  color: white;
  background: #c21c00;
  border: #c21c00;
  margin-inline: 1.2vw;
  font-family: 'Playfair Display', serif;
}

.hero1 h1{
  font-size: 55px;
  font-weight:bolder;
  margin-inline: -28vw;
}

.carousel-inner img{
  width: 50%;
}
.carousel-caption p {
  font-size: 25px;
  margin-top: 30px;
  margin-bottom: 120px;
}
/* page-2 css */

.pg-2heading h1{
  font-size: 50px;
  font-weight: 700;
  color: #c21c00;
  padding-left: 2.5vw;
  padding-bottom: 2vw;
}

.dropdowns {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 2px solid grey;
  border-radius: 1.6vw;
  margin-inline: 120px;
  width: 83%;
  box-shadow: 2px 10px 10px rgba(34, 34, 34, 0.4);

}

.dropdown button{
  font-size:30px;
  font-weight: bold;
  background:#dfdfdf;
}

.explorebtn button{
  color: white;
  background: #c21c00;
  border:red;
  border-radius: 1vw;
  padding: 5px 0;
  padding-inline: 2vw;
  font-size: 35px;
  margin: 7px 55px;
}

.dropdown-1{
  margin-left: 40px;
}

.pg-2para p{
  padding-left: 8.5vw;
  font-size: 20px;
  margin-top: 1.3vw;
}

.values {
  text-align: center;
  
}

.values h1{
  font-size: 50px;
  font-weight:bolder;
}

.pg-2 {
  background:#dfdfdf;
}

.pg-2icons{
  display: flex;
  justify-content: space-around;
}

.pg-2icons img{
  width:5.5vw;
}

.pg2-icon {
  background: white;
  border-radius: 1.5vw;
  width: 18vw;
  height:15vw;
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items:center;

}

.destination{
  text-align: center;
  margin-top: 7vw;
}

.destination h1{
  font-size: 50px;
  font-weight:bolder;
}



/* page-3 css */

.pg-3 {
  border:4px solid black;
  border-left-color: transparent;
  border-right-color: transparent;
}


section{
  margin-top: 40px;
}


.card {
  border:transparent;
  margin: 10px 4px;
  margin-top:2vw;
  margin-right: 2vw;
  transition: .6s ease;
  padding:5vw
}

.card h4 {
  font-weight: bolder;
}

.card p{
  font-size: 10px;
}

.card:hover {
  transform: scale(1.05);
}

.card-block {
   padding-top: 10px;
}

.scrollcards {
  background-color: #fff;
  overflow: auto;
  white-space: nowrap;
}

::-webkit-scrollbar {
    width: 0px;
    height: 0px;
    background: transparent;
}

div.scrollcards .card {
    display: inline-block;
    padding: 14px;
    text-decoration: none;
    height: auto; 
    width: 380px;
}




/* page-4 css */

.subs{
  background: #dfdfdf;
}

.subscribe{
  display: flex;
  background:white;
  justify-content: space-between;
  padding:10px;
  border-radius: 1vw;
  width: 50vw;
  margin-bottom: 4vw;
}

.subscribe input{
  border: white;
  height: 2.5vw;
  width: 32vw;
}

.subscribe button{
  border: #c21c00;
  background: #c21c00;
  border-radius: 1.5vw;
  color: white;
  padding: 1px 60px;
  font-weight: bold;
}

.footer{
  display: flex;
  justify-content: space-between;
  padding-bottom: 4vw;
  padding-right: 5vw;
  background: #dfdfdf;
}


.icn{
  font-size: large;
}

.footer p{
  font-size:20px;
}

.footer h2{
  font-size: 35px;
  font-weight: 700;
}

.footer img{
  width: 32vw;
  padding-left: 3vw;
}

.logofooter p{
  padding-left:7vw;
  padding-top: 2vw;
}
